<style scoped>
table {
  margin: 0 auto !important;
}
table input {
  border: none !important;
  outline: none !important;
  text-align: right !important;
}
code {
  font-size: 1.3em !important;
}
</style>

<template>
  <div>
    <Header />
    <div id="estimate" class="ui vertical stripe quote segment">
      <div class="ui equal width stackable internally celled grid">
        <div class="center aligned row">
          <div class="column">
            <h3>
              Estimate your profit
            </h3>
            <p>
              Provide web traffic and estimate profit.
              <br />
              Calculation based on below assumptions.
            </p>
            <table class="ui very basic collapsing celled table unstackable">
              <tbody>
                <tr>
                  <td>
                    GD
                  </td>
                  <td>
                    Global Difficulty
                  </td>
                  <td>
                    <vue-numeric v-model="global_difficulty"/>
                  </td>
                </tr>
                <tr>
                  <td>
                    BR
                  </td>
                  <td>
                    Block Reward
                  </td>
                  <td>
                    <vue-numeric
                    :precision="2"
                    currency="XMR"
                    v-model="block_reward"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    SH
                  </td>
                  <td>
                    Solved Hashes
                  </td>
                  <td>
                    <vue-numeric v-model="solved_hashes"/>
                  </td>
                </tr>
                <tr>
                  <td>
                    CE
                  </td>
                  <td>
                    CPU Efficiency
                  </td>
                  <td>
                    <vue-numeric
                    currency="hash/sec"
                    v-model="hashes_per_second"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    XU
                  </td>
                  <td>
                    XMR to USD
                  </td>
                  <td>
                    <vue-numeric
                    :precision="2"
                    v-model="xmrusd"
                    currency="USD"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
              </tbody>
            </table>
            <br/>
            <p>
              Reward after solving {{ this.solved_hashes }} hashes:
            </p>
            <code>
              (SH / GD) * BR = {{ this.reward.toFixed(7) }} XMR
            </code>
          </div>
          <div class="column">
            <Calculator
              :xmrusd="xmrusd"
              :miners="miners"
              :hashes="hashes_per_second"
              :reward="reward_per_one_hash"
            />
          </div>
        </div>
      </div>
    </div>

    <div class="ui vertical stripe segment">
      <div class="ui text main container cards centered">
        <h3>
          Compare Miners Efficiency
        </h3>

        <Miner
        v-for="miner, key in miners"
        :xmrusd="xmrusd"
        :reward="reward_per_one_hash"
        :miner="miner"
        :key="key"
        />
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Calculator from '@/components/Calculator'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Miner from '@/components/Miner'
import miners from '@/miners'

export default {
  data() {
    return {
      xmrusd: 0,
      block_reward: 0,
      solved_hashes: 1000000,
      global_difficulty: 0,
      hashes_per_second: 30,
    }
  },
  computed: {
    reward() {
      return (this.solved_hashes * this.block_reward) / this.global_difficulty
    },
    miners() {
      return miners.sort((a, b) => a.charge > b.charge)
    },
    reward_per_one_hash() {
      return this.reward / this.solved_hashes
    },
  },
  created() {
    this.fetchReward()
    this.fetchXMRPrice()
  },
  methods: {
    fetchXMRPrice() {
      const url = 'https://min-api.cryptocompare.com/data/price?fsym=XMR&tsyms=USD'
      this.$http.get(url).then(res => this.xmrusd = res.body.USD)
    },
    fetchReward() {
      const url = 'https://cors.io/?https://whattomine.com/coins/101.json'
      this.$http.get(url)
      .then(res => {
        console.log(res)
        this.block_reward = res.body.block_reward
        this.global_difficulty = res.body.difficulty
      })
    },
  },
  components: {
    Calculator,
    Header,
    Footer,
    Miner,
  },
}
</script>


